
html,body{height:100%
}
body{display:-ms-flexbox;display:flex
}
.container{min-height:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap;-ms-flex:1;flex:1;transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1)
}
@media (min-width: 960px){
.container{padding-left:200px
}
}
.main-header{position:relative;z-index:3
}
@media (min-width: 960px){
.main-header .md-button:first-child{display:none
}
}
.main-header .md-toolbar,.main-header .md-title{color:#fff
}
.main-header .md-title .page-title{margin-left:0
}
@media (min-width: 960px){
.main-header .md-title .page-title{margin-left:8px
}
}
@media (min-width: 960px){
.main-header .md-title span:not(.page-title){display:none
}
}
.main-sidebar.md-sidenav .md-sidenav-content{width:200px;display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column
}
@media (min-width: 960px){
.main-sidebar.md-sidenav .md-sidenav-content{top:0;pointer-events:auto;transform:translate3d(0, 0, 0);box-shadow:0 1px 5px rgba(0,0,0,0.2),0 2px 2px rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)
}
}
@media (min-width: 960px){
.main-sidebar.md-sidenav .md-backdrop{opacity:0;pointer-events:none
}
}
.main-sidebar.md-sidenav .md-toolbar{min-height:172px;border-bottom:1px solid rgba(0,0,0,0.12)
}
.main-sidebar.md-sidenav .vue-material-logo{font-size:24px
}
.main-sidebar.md-sidenav .vue-material-logo a{width:100%;display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;color:inherit;text-decoration:none
}
.main-sidebar.md-sidenav .vue-material-logo img{width:160px;margin-bottom:16px
}
.main-sidebar.md-sidenav .main-sidebar-links{overflow:auto
}
.main-sidebar.md-sidenav .main-sidebar-links .md-inset .md-list-item-container{padding-left:36px
}
.main-sidebar.md-sidenav .main-sidebar-links .md-list-item-container{font-size:14px;font-weight:500
}
.page-content{display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column;-ms-flex:1;flex:1;overflow:auto;position:relative;z-index:1
}
.page-content-wrapper{display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column;-ms-flex:1;flex:1
}
.main-content{padding:16px;-ms-flex:1;flex:1;overflow:auto
}
.md-router-enter-active,.md-router-leave-active{position:absolute;top:0;right:0;left:0;transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1)
}
@media (min-width: 960px){
.md-router-enter-active,.md-router-leave-active{left:200px
}
}
.md-router-enter,.md-router-leave-active{opacity:0
}
.md-router-enter .md-display-2{transform:translate3D(0, -36px, 0)
}
code:not(.hljs){margin-left:1px;margin-right:1px;padding:0 4px;display:inline-block;border-radius:2px;font-family:"Operator Mono", "Fira Code", Menlo, Hack, "Roboto Mono", "Liberation Mono", Monaco, monospace
}
code:not(.hljs) pre{margin:8px 0
}



